
.app-box .weui-grid {
  width: 75px;
  margin: 20px 10px;
  padding: 0;
}
.app-box .weui-grids:before {
  border-right: none;
}
.app-box .weui-grids:after {
  border-left: none;
}
.app-box .weui-grid:before {
  border-right: none;
}
.app-box .weui-grid:after {
  border-bottom: none;
}
.grid {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
.grid .grid-item {
  width: 75px;
  margin: 10px;
  position: relative;
}
.grid .touch {
  position: absolute;
  height: 100%;
  width: 100%;
}
.grid img {
  height: 45px;
  margin: 0 15px;
}
.grid p {
  width: 75px;
  text-align: center;
  font-size: 0.9rem;
}
.grid .choose {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  height: 100%;
  width: 100%;
  top: 0;
  line-height: 70px;
  text-align: center;
  font-size: 2rem;
  color: aqua;
}
.delate {
  position: absolute;
  right: 0;
  top: 0;
  background-color: #f8f8f8;
  color: blue;
  width: 60px;
  text-align: center;
  height: 45px;
  line-height: 45px;
}



